<template>
    <v-col :sm="sm" :md="md" :xl="xl" :xs="xs" :lg="lg" v-if="show">
        <slot v-if="show"></slot>
    </v-col>
</template>

<script>
export default {
    props:{
        axs:{
            type:Boolean,
            default:true,
        },
        asm:{
            type:Boolean,
            default:true,
        },
        amd:{
            type:Boolean,
            default:true,
        },
        alg:{
            type:Boolean,
            default:true
        },
        axl:{
            type:Boolean,
            default:true,
        },
        xs:Number,
        sm:Number,
        md:Number,
        xl:Number,
        lg:Number,
    },
    computed:{
        show(){
            if(this.$vuetify.breakpoint.xs & this.axs){
                return true;
            }
            if(this.$vuetify.breakpoint.sm & this.asm){
                return true;
            }
            if(this.$vuetify.breakpoint.md & this.amd){
                return true;
            }
            if(this.$vuetify.breakpoint.lg & this.alg){
                return true;
            }
            if(this.$vuetify.breakpoint.xl & this.axl){
                return true;
            }
            return false
        }
    }
}
</script>

