<template>
    <div class="tjtrade-homepage-wrapper">
        
        <!--Carousel-->
        <v-container>
            <v-row>
                <!--Cards-->
                <v-col sm="4">
                    <title-with-sharp :title="$t('home.zone')" class="tjtrade-offset-compensation"/>
                    <v-container>
                        <v-row>
                            <v-col :cols="getZoneCol" v-for="i in getZones" :key="i">
                                <zone-hover-card :zoneid="recommendZone[i]" :heavyStyle="i%2==0" :height="getZoneCardHeight"/>
                            </v-col>
                        </v-row>
                    </v-container>
                </v-col>
                <!--Slides-->
                <v-col sm="8" xl="5">
                    <title-with-sharp :title="$t('home.activity')"/>
                    <homepage-slide/>
                </v-col>
                <!--User Center-->
                <breakpoint-cond-col :asm="false" :axs="false" :amd="false" :alg="false" xl="3">
                    <v-container>
                        <v-row>
                            <title-with-sharp :title="$t('home.user_abstract')" class="tjtrade-offset-compensation"/>
                        </v-row>
                        <v-row>
                            <v-col>
                                <homepage-user-abstract-vertical/>
                                
                            </v-col>
                        </v-row>
                    </v-container>
                </breakpoint-cond-col>
            </v-row>
        </v-container>

        <!--User Detail (for xs & md)-->
        <breakpoint-cond :xl="false">
            <v-container>
                <v-row>
                    <title-with-sharp :title="$t('home.user_abstract')" class="tjtrade-offset-compensation"/>
                </v-row>
                <v-row>
                    <v-col>
                        <homepage-user-abstract/>
                    </v-col>
                </v-row>
            </v-container>
        </breakpoint-cond>
        <br/>

        <!--Samecity Waterfall-->
        <v-container>
            <v-row>
                <title-with-sharp :title="$t('home.samecity')" class="tjtrade-offset-compensation"/>
            </v-row>
            <v-row>
                <v-col>
                    <homepage-recommend-waterfall :usePlaceHolder="true">
                        <template v-slot:placeholder>
                            <zone-hover-card :zoneid="0" :listSource="'special'" :height="230" :heavyStyle="true"/>
                        </template>
                    </homepage-recommend-waterfall>
                </v-col>
            </v-row>
        </v-container>

        <!--Recommend Waterfall-->
        <v-container>
            <v-row>
                <title-with-sharp :title="$t('home.recommend')" class="tjtrade-offset-compensation"/>
            </v-row>
            <v-row>
                <v-col>
                    <homepage-recommend-waterfall :usePlaceHolder="false">
                    </homepage-recommend-waterfall>
                </v-col>
            </v-row>
        </v-container>
    </div>
</template>

<script>
import BreakpointCondCol from '../MiscellaneousWidgets/BreakpointCondCol.vue'
import BreakpointCond from '../MiscellaneousWidgets/BreakpointCond.vue'
import TitleWithSharp from '../MiscellaneousWidgets/TitleWithSharp.vue'
import ZoneHoverCard from '../MiscellaneousWidgets/ZoneHoverCard.vue'
import HomepageRecommendWaterfall from './HomepageRecommendWaterfall.vue'
import HomepageSlide from './HomepageSlide.vue'
import HomepageUserAbstract from './HomepageUserAbstract.vue'
import HomepageUserAbstractVertical from './HomepageUserAbstractVertical.vue'
export default {
    components: { 
        HomepageSlide ,
        HomepageUserAbstract,
        HomepageRecommendWaterfall,
        ZoneHoverCard,
        TitleWithSharp,
        BreakpointCond,
        HomepageUserAbstractVertical,
        BreakpointCondCol
    },
    data(){
        return{
            recommendZone:[]
        }
    },
    created(){
        let arr = [];
        for(let i=0;i<this.$store.state.zones.length;i++){
            arr.push(i)
        }
        let num = 9;
        while(this.recommendZone.length < num){
            let temp = (Math.random()*arr.length) >> 0;
            this.recommendZone.push(arr.splice(temp,1)[0]);
        }
    },
    computed:{
        getSpecialZones(){
            return this.$store.state.specialZones
        },
        getZoneCol(){
            if(this.$vuetify.breakpoint.xl){
                return 4
            }else{
                return 6
            }
        },
        getZones(){
            if(this.$vuetify.breakpoint.xl){
                return 9
            }else{
                return 4
            }
        },
        getZoneCardHeight(){
            if(this.$vuetify.breakpoint.xl){
                return 145
            }else{
                return 155
            }
        }
    }
}
</script>

