<template>
    <hover-card :additionalClass="getAdditionalClass" >
        <v-card class="tjtr-zonecard tjtr-vertical-center-blk" :class="getClass" tile :style="getHeight">
            <v-container style="display:inline-block">
                <v-row>
                    <v-col>
                        <v-icon x-large class="tjtr-scale-xlicon" :color="getIconColor">
                            {{getInfo.icon}}
                        </v-icon>
                    </v-col>
                </v-row>
                <v-row>
                    <v-col>
                        <span class="tjtr-opt-font tjtrtmp-1">
                            {{$t(getInfo.label)}}
                        </span>
                    </v-col>
                </v-row>
            </v-container>
        </v-card>
        <template v-slot:expansion>
            {{$t(getInfo.description)}}
        </template>
    </hover-card>
</template>

<script>
import HoverCard from '../MiscellaneousWidgets/HoverCard.vue'
export default {
    components: { HoverCard },
    props:{
        additionalClass:{
            type:Array,
            default:()=>{
                return ['blue']
            }
        },
        zoneid:{
            type:Number,
            default:0
        },
        heavyStyle:{
            type:Boolean,
            default:false
        },
        listSource:{
            type:String,
            default:""
        },
        height:{
            type:Number,
            default:145
        }
    },
    computed:{
        getInfo(){
            if(this.listSource==='special'){
                return this.$store.state.specialZones[this.zoneid];
            }
            else{
                return this.$store.state.zones[this.zoneid]
            }
            
        },
        getAdditionalClass(){
            if(this.heavyStyle){
                return ['white','blue--text']
            }else{
                return ['blue','white--text']
            }
        },
        getClass(){
            if(this.heavyStyle){
                return{
                    "tjtr-zonecard-light":false,
                    "tjtr-zonecard-heavy":true,
                }
            }
            return{
                "tjtr-zonecard-light":true,
                "tjtr-zonecard-heavy":false,
            }
        },
        getIconColor(){
            if(this.heavyStyle){
                return 'white'
            }else{
                return 'black'
            }
        },
        getHeight(){
            return{
                'height':this.height+"px"
            }
        }
    }
}
</script>

<style scoped lang="scss">
.tjtrtmp-1{
    font-size:22px;
    font-weight: bold;
}
</style>