<template>
    <div class="tjtr-widget-lblbtn tjtr-center-align" >
        <v-hover v-slot="{hover}">
            <table class="tjtr-center-align" style="display:inline-block;">
                <tr class="tjtr-center-align" align="center">
                    <td class="tjtr-center-align">
                        <v-icon x-large :color="hover?'primary':'default'" v-if="!dense" @click="routerGo">
                            {{icon}}
                        </v-icon>
                        <v-icon  :color="hover?'primary':'default'" v-if="dense" @click="routerGo">
                            {{icon}}
                        </v-icon>
                    </td>
                </tr>
                <tr class="tjtr-center-align" align="center">
                    <td class="tjtr-widget-lblbtn-label">
                        <v-chip :color="hover?'primary':'default'" class="tjtr-transition-all" v-if="!dense"
                        @click="routerGo">
                            {{label}}
                        </v-chip>
                        <span v-if="dense" @click="routerGo">
                            {{label}}
                        </span>
                    </td>
                </tr>
            </table>
        </v-hover>
    </div>
</template>

<script>
export default {
    props:{
        icon:String,
        label:String,
        dense:{
            type:Boolean,
            default:false
        },
        route:{
            type:String,
            default:""
        }
    },
    methods:{
        routerGo(){
            if(this.route!=""){
                this.$router.push(this.route)
            }
            
        },

    }
}
</script>

