<template>
  <div class="tjtr-clear-padding">
    <div :class="getClassA">
      <div :class="getClassB"></div>
      Hello World
      <transition name="fade">
        <span v-show="!intersecting" class="tjtr-float-right">
          <v-text-field
              class="tjtr-logo-panel-search-input tjtr-logo-panel-searchbtn-2"
              solo
              rounded
              dense
              prepend-inner-icon="mdi-magnify"
          >
          </v-text-field>
          &nbsp;
          <v-btn class="tjtr-logo-panel-searchbtn-2" color="default">
                Search
            </v-btn>
        </span>
      </transition>
    </div>
  </div>
</template>

<script>
export default {
  name:"ApplicationBar",
  props:{
    intersecting:Boolean
  },
  computed:{
    getClassA(){
      return{
        'tjtr-appbar':this.intersecting,
        'tjtr-appbar-2':!this.intersecting
      }
    },
    getClassB(){
      return{
        'tjtr-appbar-bg':this.intersecting,
        'tjtr-appbar-bg-2':!this.intersecting
      }
    }
  }
}
</script>

