<template>
    <div>
        <waterfall :line-gap="230" :watch="items">
            <waterfall-slot
                v-for="(item, index) in items.slice(0,Math.min(items.length,itemLim))"
                :width="item.width"
                :height="item.height"
                :order="index"
                :key="item.id"
                class="tjtrade-waterfall-slot-gap"
            >
                <div v-if="index<itemLim">
                    <div v-if="index<itemLim-1 && index!=items.length-1">
                        <homepage-item-showcase>
                        </homepage-item-showcase>
                    </div>
                    <div v-if="index==itemLim-1 || index==items.length-1">
                        <div v-if="usePlaceHolder">
                            <slot name="placeholder">
                            </slot>
                        </div>
                        <div v-else>
                            <homepage-item-showcase>
                            </homepage-item-showcase>
                        </div>
                    </div>
                </div>
            </waterfall-slot>
        </waterfall>
    </div>
</template>

<script>
import Waterfall from 'vue-waterfall/lib/waterfall'
import WaterfallSlot from 'vue-waterfall/lib/waterfall-slot'
import HomepageItemShowcase from './HomepageItemShowcase.vue'
export default {
    components:{
        Waterfall,
        WaterfallSlot,
        HomepageItemShowcase,
    },
    props:{
        usePlaceHolder:{
            type:Boolean,
            default:false,
        },
        itemLim:{
            type:Number,
            default:10
        }
    },
    data(){
        return{
            items:[
                {height:60,width:50,id:1},
                {height:60,width:50,id:2},
                {height:60,width:50,id:3},
                {height:60,width:50,id:4},
                {height:60,width:50,id:5},
                {height:60,width:50,id:6},
                {height:60,width:50,id:7},
                {height:60,width:50,id:8},
                {height:60,width:50,id:9},
                {height:60,width:50,id:9},
                //PlaceHolder
                {height:60,width:50,id:10},
            ]
        }
    }
}
</script>

