import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import Login from "../views/Login.vue";
import RegisterWidget from "../components/LoginComponents/RegisterWidget.vue";
import LoginWidget from "../components/LoginComponents/LoginWidget.vue";
import ResetPasswordWidget from "../components/LoginComponents/ResetPasswordWidget.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
  },
  {
    path: "/commodity/showcase",
    name: "About",
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/ItemShowcase.vue"),
  },
  {
    path: "/commodity/history",
    name: "History",
    component: () =>
      import(/* webpackChunkName: "history" */ "../views/History.vue"),
  },
  {
    path: "/commodity/bookmark",
    name: "Bookmark",
    component: () =>
      import(/* webpackChunkName: "bookmark" */ "../views/Favorite.vue"),
  },
  {
    path: "/community/notification",
    name: "Notification",
    component: () =>
      import(/* webpackChunkName: "notification" */ "../views/Notification.vue"),
  },
  {
    path: "/account/",
    name: "Login",
    component:Login,
    children: [
      {
        path: 'register',
        component: RegisterWidget
      },
      {
        path: 'login',
        component: LoginWidget
      },
      {
        path: 'resetpswd',
        component: ResetPasswordWidget
      },
    ]
  },
  {
    path: "/community/user/:id",
    name: "UserCenter",
    component: () =>
      import(/* webpackChunkName: "usercenter" */ "../views/UserCenter.vue"),
  },
  {
    path: "/commodity/sell",
    name: "Sell",
    component: () =>
      import(/* webpackChunkName: "notification" */ "../views/Sell.vue"),
  },
  {
    path: "/transaction/",
    name: "TransactionCenter",
    component: () =>
      import(/* webpackChunkName: "notification" */ "../views/Transaction.vue"),
  }
];

const router = new VueRouter({
  base: process.env.BASE_URL,
  routes,
});

export default router;
