import * as THREE from 'three';
import { TrackballControls } from 'three/examples/jsm/controls/TrackballControls.js';
import { TessellateModifier } from 'three/examples/jsm/modifiers/TessellateModifier.js';
import { FontLoader } from 'three/examples/jsm/loaders/FontLoader.js';
import { TextGeometry } from 'three/examples/jsm/geometries/TextGeometry.js';
import TessellationShader from '../shader/tessellation_shader'
let renderer, scene, camera;
let controls;
let mesh, uniforms;
const WIDTH = window.innerWidth;
const HEIGHT = window.innerHeight;
const loader = new FontLoader();

export function startRender(){
    loader.load( '/res/webgl/fonts/helvetiker_bold.typeface.json', function ( font ) {
        init( font );
        animate();
    } );
}
function init( font ) {
    camera = new THREE.PerspectiveCamera( 40, WIDTH / HEIGHT, 1, 10000 );
    camera.position.set( 0, 0, 0 );
    scene = new THREE.Scene();
    scene.background = new THREE.Color( 0x4876FF );
    //
    let geometry = new TextGeometry( 'Hello World', {
        font: font,
        size: 40,
        height: 5,
        curveSegments: 3,
        bevelThickness: 2,
        bevelSize: 1,
        bevelEnabled: true
    } );
    geometry.center();
    const tessellateModifier = new TessellateModifier( 8, 6 );
    geometry = tessellateModifier.modify( geometry );
    //
    const numFaces = geometry.attributes.position.count / 3;
    const colors = new Float32Array( numFaces * 3 * 3 );
    const displacement = new Float32Array( numFaces * 3 * 3 );
    const color = new THREE.Color();
    for ( let f = 0; f < numFaces; f ++ ) {
        const index = 9 * f;
        const h = 258/180*3.14+ 0.2*Math.random();
        const s = 0.5 + 0.5 * Math.random();
        const l = 0.5 + 0.5 * Math.random();
        color.setHSL( h, s, l );
        let px = Math.random()*0.5+0.5
        color.setRGB(px,px,px)
        const d = 10 * ( 0.5 - Math.random() );
        for ( let i = 0; i < 3; i ++ ) {
            colors[ index + ( 3 * i ) ] = color.r;
            colors[ index + ( 3 * i ) + 1 ] = color.g;
            colors[ index + ( 3 * i ) + 2 ] = color.b;
            displacement[ index + ( 3 * i ) ] = d;
            displacement[ index + ( 3 * i ) + 1 ] = d;
            displacement[ index + ( 3 * i ) + 2 ] = d;
        }
    }
    geometry.setAttribute( 'customColor', new THREE.BufferAttribute( colors, 3 ) );
    geometry.setAttribute( 'displacement', new THREE.BufferAttribute( displacement, 3 ) );
    //
    uniforms = {
        amplitude: { value: 0.0 },
        time: { value: 1.0 }
    };
    const shaderMaterial = new THREE.ShaderMaterial( {
        uniforms: uniforms,
        vertexShader: TessellationShader.vertexShader,
        fragmentShader: TessellationShader.fragmentShader
    } );
    //
    mesh = new THREE.Mesh( geometry, shaderMaterial );
    scene.add( mesh );
    renderer = new THREE.WebGLRenderer( { antialias: true } );
    renderer.setPixelRatio( window.devicePixelRatio );
    const container = document.getElementById( 'tjtr-webgl-displayer-t0' );
    renderer.setSize( container.parentElement.scrollWidth, container.parentElement.scrollHeight );
    container.appendChild( renderer.domElement );
    controls = new TrackballControls( camera, renderer.domElement );
    //
    window.addEventListener( 'resize', onWindowResize );
    window.exposedListener = onWindowResize
}
function onWindowResize() {
    let container = document.getElementById( 'tjtr-webgl-displayer-t0' );
    camera.aspect = container.parentElement.scrollWidth / container.parentElement.scrollHeight;
    camera.updateProjectionMatrix();
    renderer.setSize( container.parentElement.scrollWidth, container.parentElement.scrollHeight );
    console.log(container.parentElement.scrollWidth)
    console.log(container.parentElement.scrollHeight)
}
function animate() {
    requestAnimationFrame( animate );
    render();
}
function render() {
    const time = Date.now() * 0.001;
    uniforms.amplitude.value = 1.0 + Math.cos( time * 0.7 );
    camera.position.x =Math.sin(time*0.115)*400
    camera.position.z = 0+Math.cos(time*0.115)*400
    camera.position.y = 0;
    camera.lookAt(0,0,0)
    
    renderer.render( scene, camera );
}
