<template>
    <span >
        <slot v-if="show"></slot>
    </span>
</template>

<script>
export default {
    props:{
        xs:{
            type:Boolean,
            default:true,
        },
        sm:{
            type:Boolean,
            default:true,
        },
        md:{
            type:Boolean,
            default:true,
        },
        lg:{
            type:Boolean,
            default:true
        },
        xl:{
            type:Boolean,
            default:true,
        }
    },
    computed:{
        show(){
            if(this.$vuetify.breakpoint.xs & this.xs){
                return true;
            }
            if(this.$vuetify.breakpoint.sm & this.sm){
                return true;
            }
            if(this.$vuetify.breakpoint.md & this.md){
                return true;
            }
            if(this.$vuetify.breakpoint.lg & this.lg){
                return true;
            }
            if(this.$vuetify.breakpoint.xl & this.xl){
                return true;
            }
            return false
        }
    }
}
</script>

