<template>
    <div class="tjtrade-subsystem-bar" :class="getXLRestriction">
        <v-container>
            <v-row no-gutters class="tjtr-center-align">
                <v-col v-for="i in getFunctions" :key="i.id">
                    <labeled-button
                    :icon="i.icon"
                    :label="$t(i.label)"
                    :route="i.router"
                    />
                </v-col>
            </v-row>
        </v-container>
    </div>
</template>

<script>
import LabeledButton from '../MiscellaneousWidgets/LabeledButton.vue'
export default {
    components: { LabeledButton },
    computed:{
        getFunctions(){
            return this.$store.state.homepage.guidebar
        },
        getXLRestriction(){
            if(this.$vuetify.breakpoint.xl){
                return{
                'tjtr-xl-restriction':'true'
                }
            }else{
                return {}
            }
        }
    },
    
}
</script>

