<template>
    <div class="tjtr-login-wrapper tjtr-clear-all-padding-force">
        <v-container class="tjtr-clear-all-padding-force tjtr-extend-force tjtr-extend-maxwidth-force tjtr-height-full-force">
            <v-row class="tjtr-clear-all-padding-force tjtr-extend-force tjtr-height-full-force tjtr-animation">
                <v-col :md="11-getLoginCol" :lg="12-getLoginCol" class="tjtr-clear-all-padding-force tjtr-height-full-force tjtr-animation">
                    <div class="tjtr-login-style-placeholder tjtr-animation">
                        <login-animation/>
                    </div>
                </v-col>
                <v-col :md="getLoginCol+1" :lg="getLoginCol" 
                class="tjtr-clear-all-padding-force tjtr-height-full-force tjtr-vertical-center-blk tjtr-center-align tjtr-animation">
                    <transition name="slide-fade" mode="out-in">
                        <router-view/>
                    </transition>
                </v-col>
            </v-row>
        </v-container>
    </div>
</template>

<script>
import WebglAnimation from '../MiscellaneousWidgets/WebglAnimation.vue'
import LoginAnimation from './LoginAnimation.vue'
export default {
    components:{
        LoginAnimation,
        WebglAnimation
    },
    computed:{
 
        isLogin(){
            return this.$route.path=== "/account/login"
        },
        getLoginCol(){
            if(this.$route.path === "/account/login"){
                return 3
            }else{
                return 4
            }
        }
    }
}
</script>

