<template>
  <v-app>
    <v-main v-if="!isLogin">
      <vue-particles
        color="#d5d5d5"
        :particleOpacity="0.7"
        :particlesNumber="80"
        shapeType="circle"
        :particleSize="4"
        linesColor="#d5d5d5"
        :linesWidth="1"
        :lineLinked="true"
        :lineOpacity="0.4"
        :linesDistance="150"
        :moveSpeed="3"
        :hoverEffect="true"
        hoverMode="grab"
        :clickEffect="true"
        clickMode="push"
        class="tjtr-particle"
      >
      </vue-particles>
      <application-bar :intersecting="intersecting"/>
      <intersection-notifier
    v-intersect="onIntersect"/>
      <!--Logo Panels-->
      <transition name="faster-fade" mode="out-in">
        <div key="1" v-if="!isUserCenter">
            <logo-panel/>
        </div>
        <div key="2" v-else>
          <user-center-banner/>
        </div>
      </transition>
      <collapse-transition>
        <div v-if="!isUserCenter">
          <!--Guide Bar-->
          <homepage-subsystem-bar/>
          <v-divider/>
        </div>
      </collapse-transition>
      <transition name="slide-fade" mode="out-in">
        <router-view :class="getXLRestriction"/>
      </transition>
      <v-fade-transition>
        <return-top-widget v-if="!intersecting"/>
      </v-fade-transition>
    </v-main>
    <v-main v-else>
      <router-view :class="getXLRestriction"/>
    </v-main>
    <footer-widget/>
  </v-app>
</template>

<script>
import FooterWidget from './components/FooterComponents/FooterWidget.vue';
import HomepageSubsystemBar from './components/HomepageComponents/HomepageSubsystemBar.vue';
import ApplicationBar from './components/LayoutWidgets/ApplicationBar.vue';
import LogoPanel from './components/LayoutWidgets/LogoPanel.vue';
import IntersectionNotifier from './components/MiscellaneousWidgets/IntersectionNotifier.vue';
import { CollapseTransition } from "@ivanv/vue-collapse-transition"
import UserCenterBanner from './components/CommunityComponents/UserCenterBanner.vue';
import ReturnTopWidget from './components/LayoutWidgets/ReturnTopWidget.vue';
export default {
  components: { ApplicationBar, FooterWidget,IntersectionNotifier, LogoPanel, HomepageSubsystemBar,CollapseTransition, UserCenterBanner, ReturnTopWidget },
  name: "App",
  data(){
    return{
      intersecting: true
    }
  },
  methods:{
    onIntersect(entries, observer) {
      this.intersecting = entries[0].isIntersecting
      let r=observer;
      let g=false;
      if(g){
        console.log(r)
      }
    }
  },
  computed:{
    isUserCenter(){
      let reg = /^\/community\/user\/.*/i
      if(this.$route.path.search(reg)!=-1){
        return true;
      }
      return false;
    },
    isLogin(){
      let reg = /^\/account\/.*/i
      if(this.$route.path.search(reg)!=-1){
        return true;
      }
      return false;
    },
    getXLRestriction(){
      if(this.$vuetify.breakpoint.xl){
        return{
          'tjtr-xl-restriction':'true'
        }
      }else{
        return {}
      }
    }
  }
};
</script>
