<template>
    <div class="tjtr-widget-card-hover">
        <v-hover v-slot:default="{ hover }">
            <v-card
            class="mx-auto"
            color="grey lighten-4"
            max-width="600"
            >
                <div v-if="!imageMode">
                    <slot></slot>
                    <v-expand-transition>
                        <div
                            v-if="hover" 
                            class="d-flex transition-fast-in-fast-out darken-2 v-card--reveal "
                            :class="getHoverClass"
                            style="height: 100%;"
                        >
                            <slot name="expansion"></slot>
                        </div>
                    </v-expand-transition>
                </div>
                <div v-if="imageMode">
                    <v-img
                    :aspect-ratio="1"
                    class="tjtr-proto-hover-card-img"
                    :src="imageSrc"
                    >
                        <v-expand-transition>
                            <div
                                v-if="hover" 
                                class="d-flex transition-fast-in-fast-out darken-2 v-card--reveal "
                                :class="getHoverClass"
                                style="height: 100%;"
                            >
                                <slot name="expansion"></slot>
                            </div>
                        </v-expand-transition>
                    </v-img>
                </div>
                <v-card-text style="position: relative;" class="tjtr-hovertext" v-if="appendAfter">
                    <div>
                        <slot name="hover_after" ></slot>
                    </div>
                </v-card-text>
            </v-card>
            
        </v-hover>
    </div>
</template>

<script>
export default {
    props:{
        additionalClass:{
            type:Array,
            default:()=>{
                return []
            }
        },
        appendAfter:{
            type:Boolean,
            default:false,
        },
        imageMode:{
            type:Boolean,
            default:false,
        },
        imageSrc:{
            type:String,
            default:""
        }
    },
    data(){
        return{
            bindClass:{}
        }
    },
    created(){
        for(let i=0;i<this.additionalClass.length;i++){
            this.bindClass[this.additionalClass[i]] = true
        }
    },
    computed:{
        getHoverClass(){
            return this.bindClass
        }
    }
}
</script>

